<template>
  <div class="flex-box quan">
    <el-card class="box-card" style="height: 28rem">
      <div slot="header" class="clearfix">
        <span>用户注册</span>
      </div>
      <div>
        <el-form :rules="rules" ref="user" :model="user" label-width="70px">
          <el-form-item label="登录名" prop="username">
            <el-input
              v-model="user.username"
              placeholder="请输入登录名"
            ></el-input>
          </el-form-item>
          <el-form-item label="昵称" prop="nickname">
            <el-input placeholder="昵称" v-model="user.nickname"></el-input>
          </el-form-item>
          <el-form-item prop="password" label="密码">
            <el-input
              v-model="user.password"
              placeholder="请输入密码"
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item label-width="79px" label="确认密码" prop="password2">
            <el-input
              v-model="user.password2"
              placeholder="请输入密码"
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item class="flex-box" label-width="0px">
            <el-button type="primary" @click="submitForm">创建</el-button>
            <el-button type="success" @click="goLogin">登录</el-button>
            <el-button @click="resetForm('user')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <div @click="ce">asas</div>
  </div>
</template>

<script>
import tools from '@/js/tools';
// import { delete } from 'vue/types/umd';
export default {
  data() {
    let valiatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.user.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      user: {
        username: '',
        password: '',
        nickname: '',
        password2: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入登录名', trigger: 'blur' }
        ],
        nickname: [{ required: true, message: '请输入昵称', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
        password2: [{ validator: valiatePass2, trigger: 'blur' }]
      }
    };
  },
  methods: {
    submitForm() {
      let commitUser = JSON.parse(JSON.stringify(this.user));
      delete commitUser.password2;
      commitUser.password = tools.md5(commitUser.password);
      tools.ajax('/user/auth/reg', commitUser, (data) => {
        if (data.success) {
          this.$message(data.message);
          this.$router.push('/user/login');
        }
        this.resetForm('user');
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    ce() {
      let username = 'chengbelief';
      this.$router.push({
        path: '/user/main/' + username
      });
    },
    goLogin() {
      this.$router.push('/user/login');
    }
  }
};
</script>

<style socep>
.box-card {
  min-width: 35rem;
}
.box-card {
  min-height: 25rem;
}
.flex-box button {
  margin: 0 2rem;
}
</style>
